"use client";
import React, { useEffect, useState } from "react";
import SearchIC from "@/assets/svg/search";
import ArrowIC from "@/assets/svg/arrowdown";
import { Body2 } from "../Typography/Body.styles";
import { usePathname, useRouter } from "next/navigation";
import { H5 } from "../Typography/Heading.styles";
import SearchBar from "./Search/SearchBar";

interface CateProps {
  id: number;
  tile: string;
  documentId: string;
}

const Header = () => {
  const [open, setOpened] = useState<boolean>(false);
  const [isSearch, setIsSearch] = useState<boolean>(true);
  const [cate, setCate] = useState<CateProps[]>([]);
  const router = useRouter();
  const pathname = usePathname();

  const handleGetCate = async () => {
    try {
      const response = await fetch(
        "http://localhost:1337/api/cates?populate=*",
        {
          method: "GET",
        }
      );
      const data = await response.json();
      setCate(data.data);
    } catch (error) {
      console.log(error);
    }
  };

  const handleSearchByCate = (id: string) => {
    router.push(`/${id}`);
  };

  useEffect(() => {
    handleGetCate();
  }, []);

  if (cate.length == 0) return null;

  return (
    <header className="flex flex-row w-full bg-black h-[58px] items-center justify-center">
      {!isSearch ? (
        <ul className="flex flex-row items-center  gap-[63px] h-5 select-none">
          <li className="cursor-pointer">
            <a href="/">
              <H5 $size={18} $color={pathname === "/" ? "#EA8E31" : "#fff"}>
                TRANG CHỦ
              </H5>
            </a>
          </li>
          <li className="cursor-pointer">
            <a href="/about">
              {" "}
              <H5
                $size={18}
                $color={pathname === "/about" ? "#EA8E31" : "#fff"}
              >
                GIỚI THIỆU
              </H5>
            </a>
          </li>
          <li
            className="relative cursor-pointer inline-block"
            onClick={() => setOpened(!open)}
          >
            <div className="box flex flex-row gap-3 items-center">
              <H5
                $size={18}
                $color={pathname.startsWith("/topic") ? "#EA8E31" : "#fff"}
              >
                CHỦ ĐỀ
              </H5>
              <ArrowIC
                fill={pathname.startsWith("/topic") ? "#EA8E31" : "#fff"}
              />
            </div>

            {open && (
              <div className="absolute top-full left-0 mt-6 w-40 bg-black text-white shadow-lg py-2 z-50 rounded-2xl">
                <div>
                  {cate.map((item, i) => (
                    <div
                      key={i}
                      className="px-4 py-2 cursor-pointer"
                      onClick={() => handleSearchByCate(item.documentId)}
                    >
                      <Body2 $color="#fff" $hoverColor="#EA8E31">
                        {item.tile}
                      </Body2>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </li>
          <li className="cursor-pointer">
            <a href="/FAQ">
              <H5 $size={18} $color={pathname === "/FAQ" ? "#EA8E31" : "#fff"}>
                HỎI ĐÁP
              </H5>
            </a>
          </li>
          <li
            className="cursor-pointer"
            onClick={() => {
              setIsSearch(true);
            }}
          >
            <SearchIC />
          </li>
        </ul>
      ) : (
        <SearchBar />
      )}
    </header>
  );
};

export default Header;
