import React from "react";
import {
  ButtonSearch,
  Input,
  SearchBarContainer,
  SearchMainField,
} from "./SearchBar.styles";
import SearchIC from "@/assets/svg/search";
import Button from "@/components/Button/button";
const SearchBar = () => {
  return (
    <SearchBarContainer>
      <SearchMainField>
        <div className="flex flex-0 items-center">
          <SearchIC />
        </div>
        <Input placeholder="Tìm kiếm" />
        <ButtonSearch $primary={false}>Tìm kiếm</ButtonSearch>
      </SearchMainField>
      <button className="text-white text-3xl">x</button>
    </SearchBarContainer>
  );
};

export default SearchBar;
